import { motion } from "framer-motion";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircle } from "@fortawesome/free-solid-svg-icons";
import { red } from "@mui/material/colors";
import { useState } from "react";

interface IExperienceContentProps {
    title: string;
    iconPath: string;
    notableThings: string[][];
    clickURL: string;
}

const ExperienceContent: React.FC<IExperienceContentProps> = ({ title, iconPath, notableThings, clickURL }) => {
    const [selection, setSelection] = useState(0);
    let selectedNotableThing = notableThings[selection];
    return (
        <motion.div
            className="experienceContent altGrayBackgroundColor"
            initial={{ opacity: 0, scale: 0.98 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 1, ease: "easeOut" }}
        >
            <a href={clickURL}>
                <img
                    className="experienceImage"
                    src={iconPath}
                    width="auto"
                    height="250px" />
            </a>
            <ExperienceBody
                title={title}
                selectedNotableThing={selectedNotableThing}
            />
            <BottomSwitchers
                notableThings={notableThings}
                thingSelectedIndex={selection}
                setSelection={setSelection}
            />
        </motion.div>
    );
}
interface IBottomSwitchersProp {
    notableThings: string[][];
    thingSelectedIndex: number;
    setSelection: React.Dispatch<React.SetStateAction<number>>;
}
const BottomSwitchers: React.FC<IBottomSwitchersProp> = ({ notableThings, thingSelectedIndex, setSelection }) => {
    return (
        <div className="bottomSwitchers" style={{ color: "#099" }}>
            {notableThings.map((item, index) => (
                index == thingSelectedIndex ? (
                    <span onClick={() => setSelection(index)}>
                        <FontAwesomeIcon icon={faCircle} className="bottomSwitcherCircle turqoiseColor" />
                    </span>
                )
                    :
                    <span onClick={() => setSelection(index)}>
                        <FontAwesomeIcon icon={faCircle} className="bottomSwitcherCircle bottomSwitcherHover jetColor" />
                    </span>
            ))
            }
        </div>
    );
}
interface IExperienceBodyProps {
    title: string;
    selectedNotableThing: string[];
}
const ExperienceBody: React.FC<IExperienceBodyProps> = ({ title, selectedNotableThing }) => {
    return (
        <span className="experienceBody">
            <h2
                className="experienceTitle">
                {title}
            </h2>
            <NotableThing
                notableThing={selectedNotableThing} />
        </span>
    );
}
interface INotableThingProp {
    notableThing: string[];
}
const NotableThing: React.FC<INotableThingProp> = ({ notableThing }) => {
    return (
        <div>
            {notableThing.map((item, index) => (
                <p>{item}</p>
            )
            )
            }
        </div>
    );
}

export default ExperienceContent