const EducationSection: React.FC = () => {
    return (
        <div className="experienceSection">
            {collegeItems.map((item) => (
                <ul>
                    <ExperienceContent
                        key={item.id}
                        title={item.title}
                        iconPath={item.iconPath}
                        notableThings={item.notableThings}
                        clickURL={item.clickURL}
                    />
                </ul>
            ))}
        </div>
    );
}
export default EducationSection;