import { motion } from "framer-motion";

const AboutMeSection: React.FC = () => {
    return (
        <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ duration: 1, ease: "easeOut" }}>
            <LifeEventSection
                displayImagePath={require("../images/engineerGraphic.png")}
                largeHeader="Shiloh Minnix"
                mediumHeader="Software Developer"
                bodyLines={["I have a passion for creating and designing scalable, meaningful digital solutions to satisfy the complex needs of businesses."]}
                invertOrder={false}
            />
            <LifeEventSection
                displayImagePath={require("../images/tennessee.png")}
                largeHeader=""
                mediumHeader="Upbringing"
                bodyLines={["Growing up in a small town in Elizabethton, Tennesee, I focused my early life on Marching Band in high school and programming in my free time.",
                    "I had my first experience with programming using JavaScript when I was 13 years old, through Codecademy."
                ]}
                invertOrder={true}
            />
            <LifeEventSection
                displayImagePath={require("../images/allanHancockIcon.png")}
                largeHeader=""
                mediumHeader="Uproot and Educate"
                bodyLines={["Upon graduation, my mom offered to move to California with her, where I could go to a well-rated community college for Computer Science.",
                    "I took her up on it, and received an Associates of Computer Science from Allan Hancock College.",
                    "I was then able to transfer to a 4 year university."
                ]}
                invertOrder={false}
            />
            <LifeEventSection
                displayImagePath={require("../images/uciIcon.png")}
                largeHeader=""
                mediumHeader="The Next Level"
                bodyLines={["My university of choice was University of California, Irvine.",
                    "I played semi-pro for the school Dota 2 team, participating in many tournaments.",
                    "I studied and did personal projects to eventually graduate with a degree in Computer Game Science."
                ]}
                invertOrder={true}
            />
            <LifeEventSection
                displayImagePath={require("../images/EpicSystems.png")}
                largeHeader=""
                mediumHeader="Empowered to Make a Difference"
                bodyLines={["I was recruited into Epic Systems, an electronic health record (EHR) management software company.",
                    "I am a full stack software developer, on the Referrals & Authorizations team.",
                    "On this team, I specialize in electronic medical prior authorization (eMPA).",
                    "This feature serves to automate prior authorization for specialty care referrals."
                ]}
                invertOrder={false}
            />
        </motion.div>

    );
}
interface ILifeEventSectionProps {
    displayImagePath: string;
    largeHeader: string;
    mediumHeader: string;
    bodyLines: string[];
    invertOrder: boolean;
}

const LifeEventSection: React.FC<ILifeEventSectionProps> = ({ displayImagePath, largeHeader, mediumHeader, bodyLines, invertOrder }) => {
    return (
        <div className="aboutMeSection padTop150">
            {invertOrder ? (
                <>
                    <img
                        src={displayImagePath}
                        width="auto"
                        height="250px"
                        className="invertedColorImage rightAlignedImage"
                    />
                    <span className="aboutMeText">
                        <span className="whiteColor largeHeader" >{largeHeader}</span>
                        <p className="turqoiseColor mediumHeader">{mediumHeader}</p>
                        {bodyLines.map((line) => (
                            <p>{line}</p>
                        )
                        )}
                    </span>
                </>
            ) :
                (
                    <>
                        <span className="aboutMeText">
                            <span className="whiteColor largeHeader" >{largeHeader}</span>
                            <p className="turqoiseColor mediumHeader">{mediumHeader}</p>
                            {bodyLines.map((line) => (
                                <p>{line}</p>
                            )
                            )}
                        </span>
                        <img
                            src={displayImagePath}
                            width="auto"
                            height="250px"
                            className="invertedColorImage rightAlignedImage"
                        />
                    </>
                )}
        </div>
    );
}


export default AboutMeSection