import ExperienceContent from "./ExperienceContent";
const EpicSystemsExperiences: string[][] = [
    ["I specialize in the X12/278 specification, which is widely used in the healthcare industry for electronic data interchange (EDI).",
        "In this role, I work with this specification to facilitate the exchange of healthcare-related information, particularly for Prior Authorization requests.",
        "I design, implement, and troubleshoot X12-based transaction sets, ensuring seamless integration between healthcare providers, payers, and clearinghouses.",
        "This specialization allows me to develop a deep understanding of interoperability standards in healthcare IT and strengthens my ability to manage complex, industry-specific data formats. It also sharpens my skills in data mapping, system integration, and problem-solving, all of which are valuable when working with diverse technologies in any software development or IT role."],
    ["I regularly perform bug fixes to maintain and improve software functionality, troubleshooting issues, reviewing code, and ensuring fixes are effective without introducing new problems.",
        "I also actively participate in SCRUM meetings, including daily stand-ups, sprint planning, and retrospectives. This allows me to provide progress updates, identify blockers, and help prioritize tasks, ensuring alignment with project goals.",
        "This experience strengthens my understanding of agile methodologies, team collaboration, and continuous improvement—all essential elements of successful software development."],
    ["One of my key projects at Epic Systems involves implementing Blue Cross Blue Shield (BCBS) \"BlueCarding\" for Electronic Medical Prior Authorization (eMPA) using the X12 278R specification.",
        "This project is crucial as it enables host blues to re-route authorization requests to the correct home blue over Epic's Payer Platform.",
        "The implementation is important for improving the efficiency and accuracy of the prior authorization process, ensuring that authorization requests are handled by the appropriate network, reducing delays, and minimizing the risk of errors. By utilizing the X12 278R specification, we ensure seamless data exchange between systems, maintaining compliance with industry standards. This project not only streamlines operations for healthcare providers but also helps enhance the overall interoperability and functionality of the Payer Platform."]
];

const professionalItems = [
    { id: 1, title: "Epic Systems", iconPath: require("../images/EpicSystems.png"), notableThings: EpicSystemsExperiences, clickURL: "https://www.epic.com/" },
    //{ id: 2, title: "Allan Hancock College", iconPath: require("../images/allanHancock.png"), notableThings: AllanHancockExperiences }
];


const ProfessionalSection: React.FC = () => {
    return (
        <div className="experienceSection">
            <ul>
                {professionalItems.map((item) => (
                    <ExperienceContent
                        key={item.id}
                        title={item.title}
                        iconPath={item.iconPath}
                        notableThings={item.notableThings}
                        clickURL={item.clickURL}
                    />
                ))}
            </ul>
        </div>
    );
}

export default ProfessionalSection